body {
  min-height: 100vh;
}

ul,
ol {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

.yalla-header .navbar {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 99999;
  box-shadow: 0 1px 3px #000;
  background-color: #ff5a00;
  color: #ffffff;
}
.yalla-header .navbar .yalla-header__logo-box .navbar-brand img {
  width: 120px;
}
.yalla-header .navbar .main-navbar .nav-item-close {
  display: none;
}
.yalla-header .navbar .main-navbar li a,
.yalla-header .navbar .main-navbar .sign-nav li a {
  color: #ffffff;
}
.yalla-header .navbar .sign-nav form .search-container {
  margin-top: 5px;
  border-radius: 7px;
  overflow: hidden;
  position: relative;
}
.yalla-header .navbar .sign-nav form .search-container .search-input {
  width: 100%;
  height: 35px;
  padding: 2px 12px;
  border-radius: 25px;
  font-size: 14px;
  border: 1px solid #a6a6a6;
}
.yalla-header .navbar .sign-nav form .search-container .search-input:focus-visible {
  outline: none;
}
.yalla-header .navbar .sign-nav form .search-container .search-button {
  position: absolute;
  background: transparent;
  border: none;
  left: 8px;
  top: 50%;
  cursor: pointer;
  -webkit-transform: translateY(-43%);
  transform: translateY(-43%);
}
.yalla-header .navbar .sign-nav form .search-container .search-button svg path {
  fill: #838383;
}
.yalla-header .navbar .sign-nav .nav-item .img-language {
  width: 30px;
  height: 30px;
}
.yalla-header .navbar .sign-nav .nav-item a {
  color: #fff;
}
.yalla-header .navbar .sign-nav .nav-item a svg path {
  fill: #ffffff;
}
.yalla-header .search-mobile,
.yalla-header .show-search {
  display: none;
}
.yalla-header .navbar .sign-nav .nav-item .btn-lang img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
}

@media (min-width: 992px) {
  .yalla-header .navbar .sign-nav .nav-item .sign {
    border-radius: 5px;
    border: 1px solid #ffffff;
    margin-left: 5px;
    margin-right: 9px;
    padding: 4px 8px;
    margin-top: 5px;
  }

  .navbar-expand-lg .navbar-nav .dropdown-menu {
    background-color: #ff5a00;
    top: 55px;
  }
  .navbar-expand-lg .navbar-nav .dropdown-menu .dropdown-item:hover {
    color: #ff5a00;
  }
}
@media (max-width: 991.98px) {
  .yalla-header .navbar {
    padding: 0.1rem 1rem;
  }
  .yalla-header .navbar .nav-box {
    justify-content: center;
  }
  .yalla-header .navbar .navbar-collapse {
    background-color: #fff;
    position: absolute;
    top: 0;
    right: -80%;
    color: #424648;
    width: 80%;
    height: 100vh;
    display: block;
    transition: all 0.3s ease-in-out;
    z-index: 9999999;
    overflow: scroll;
  }
  .yalla-header .navbar .navbar-collapse .nav-item {
    padding: 1px 5px;
  }
  .yalla-header .navbar .navbar-collapse .nav-item.screen-search {
    display: none;
  }
  .yalla-header .navbar .navbar-collapse .nav-item:not(:last-of-type) {
    border-bottom: 1px solid #d1cece;
  }
  .yalla-header .navbar .navbar-collapse .nav-item a {
    color: #222 !important;
  }
  .yalla-header .navbar .navbar-collapse .nav-item .btn-lang {
    text-align: start;
  }
  .yalla-header .navbar .navbar-collapse .nav-item-close {
    display: block;
    border-bottom: none !important;
    padding: 15px 15px;
  }
  .yalla-header .navbar .navbar-collapse .nav-item-close .btn-close {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    border: 1px solid #222;
    background-color: transparent;
  }
  .yalla-header .navbar .navbar-collapse.show {
    right: 0%;
    transition: all 0.3s ease-in-out;
  }
  .yalla-header .navbar .over-lay {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.385);
    z-index: 9999998;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
  }
  .yalla-header .navbar .overlay-block {
    visibility: visible;
    opacity: 1;
  }
  .yalla-header .navbar-toggler {
    position: absolute;
    right: -4px;
    top: 7px;
  }
  .yalla-header .navbar-toggler:focus {
    outline: none;
  }
  .yalla-header .navbar-toggler:focus-visible {
    outline: none;
  }
  .yalla-header .navbar-toggler .navbar-toggler-icon {
    background-color: #ffffff;
    display: block;
    width: 30px;
    height: 2px;
    margin: 6px 0;
  }
  .yalla-header .search-mobile {
    display: none;
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0;
    bottom: 0px;
    z-index: 9999999;
    background-color: #fff;
  }
  .yalla-header .search-mobile .search-container {
    margin-top: 15px;
  }
  .yalla-header .search-mobile .search-container .search-input {
    width: 100%;
    padding: 0 20px;
  }
  .yalla-header .search-mobile .search-container .search-input:focus-visible {
    outline: none;
  }
  .yalla-header .search-mobile .search-container .search-input {
    border: none;
  }
  .yalla-header .search-mobile .search-container .close-search {
    background-color: transparent;
    border: none;
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 99999999;
  }
  .yalla-header .search-mobile .search-container .close-search:focus {
    outline: 1px dotted;
  }
  .yalla-header .search-mobile .search-container .close-search svg path {
    fill: #424648;
  }
  .yalla-header .search-mobile--b {
    display: block;
  }
  .yalla-header .show-search {
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;
    z-index: 9999997;
    background-color: transparent;
    border: none;
  }
  .yalla-header .show-search:focus {
    outline: 1px dotted;
  }
  .yalla-header .show-search svg path {
    fill: #ffffff;
  }
}
